import React from 'react';
import Lightbox from 'react-lightbox-component';
import { getAttchaments, UploadAttach } from './authProvider'
import { Button } from 'react-admin';
import PublishIcon from '@material-ui/icons/Publish';
import Imageuploader from '../components/imageUploader'
import ImageDeleter from '../components/ImageDeleter'
import DeleteIcon from '@material-ui/icons/Delete';

export default function AttachmentsShow(props) {

    const [url, setUrl] = React.useState([])
    const [open, setOpen] = React.useState(false)
    const [open1, setOpen1] = React.useState(false)
    const[onDrop,setonDrop]= React.useState(null)

async function onSend (event){
  await  UploadAttach(event, url.length, onDrop)

}

    React.useEffect(
        async () => {
            let Images = []
            let url = await getAttchaments(props.record.id, true)


            url.forEach(i => {

                let img = {}
                img.src = i
                Images.push(img)
            })

            setUrl(Images)



        }, [])

    return (<>
        <Button
            label="Upload"
            onClick={() => setOpen(!open)}>
            <PublishIcon />
        </Button>
      {url.length!==0 &&  <Button
            label="Delete Picture"
            onClick={() =>setOpen1(!open1) }
            >
            <DeleteIcon />
        </Button>}
        <div>
            {url !== [] && <Lightbox images={url} />}
        </div>
        <Imageuploader onDrop={(event)=>{setonDrop(event)}} onPicSend={()=>onSend(props.record.id)} open={open} onCancel={() => { setOpen(!open) }} />
        <ImageDeleter open={open1}  onCancel={() => setOpen1(!open1)}/>
    </>
    );
}
